[data-expander-content] {
  overflow: hidden;
  height: 0;
}
[data-expander-content][data-expander-state="closed"] > * {
  display: none;
}
[data-expander-state="open"] {
  height: auto;
}
[data-expander-state="opening"],
[data-expander-state="closing"] {
  transition-property: height;
  transition-duration: 300ms;
}
